<template>
    <div class="overview-content" ref='overviewContent' @click="closeSearch">
        <my-content-title :title='title'></my-content-title>
        <div class="title-control">
            <div class="setFont">
                <i class="iconfont icon-font iconFontSize" title="设置标题大小" @click="isShowFont = true"></i>
                <div class="choose-panel">
                    <div class="choosed" @click="isShowFont = true">
                        <span class="choose-circle"></span>
                        <span class="choose-text">{{overviewFont}}像素</span>
                    </div>
                    <div v-for="item in fontList" class="fontDiv" @click = "pickFont(item)" v-show="isShowFont">{{item}}像素</div>
                </div>
            </div>
            <div class="control-block">
                <div class="icon-block">
                    <i class="iconfont " :class="item.icon" :title="item.title" v-for="(item,index) in iconList" @click="calcPos(index,2)"></i>
                </div>
            </div>
        </div>
        <div class="overview-block">
            <div class="overview-search">
                <!--一级组织-->
                <div class="tree-search">
                    <div class="search" @click.stop="addClass($event)">
                        <input class="over-search-input" type="text" placeholder="搜索组织" ref="searchInput1" @input="searchFolder($event,1)">
                        <i class="iconfont icon-zhongzi-zhankai down-icon" @click="openList(1,1)"></i>
                    </div>
                    <div class="search-list-div" v-show="searchFolderList1.length>0 && isShow1" @click.stop>
                        <ul class="search-result">
                            <li class="search-li" v-for="(item,index) in sortFolderList1" :folderId="item.t_id" @click="selectFolder(item,$event,1,2)"><i class="iconfont icon-zhongzi_duigou gou-icon"></i>{{item.t_name}}</li>
                        </ul>
                    </div>
                </div>
                <!--二级组织-->
                <div class="tree-search">
                    <div class="search" @click.stop="addClass($event)" v-show="searchFolderList2.length>0">
                        <input class="over-search-input" type="text" placeholder="搜索组织" ref="searchInput2" @input="searchFolder($event,2)">
                        <i class="iconfont icon-zhongzi-zhankai down-icon" @click="openList(2,1)"></i>
                    </div>
                    <div class="search-list-div" v-show="searchFolderList2.length>0 && isShow2" @click.stop>
                        <ul class="search-result">
                            <li class="search-li" v-for="(item,index) in sortFolderList2" :folderId="item.t_id" @click="selectFolder(item,$event,2,2)"><i class="iconfont icon-zhongzi_duigou gou-icon"></i>{{item.t_name}}</li>
                        </ul>
                    </div>
                </div>
                <!--三级组织-->
                <div class="tree-search">
                    <div class="search" @click.stop="addClass($event)" v-show="searchFolderList3.length>0">
                        <input class="over-search-input" type="text" placeholder="搜索组织" ref="searchInput3" @input="searchFolder($event,3)">
                        <i class="iconfont icon-zhongzi-zhankai down-icon" @click="openList(3,1)"></i>
                    </div>
                    <div class="search-list-div" v-show="searchFolderList3.length>0 && isShow3" @click.stop>
                        <ul class="search-result">
                            <li class="search-li" v-for="(item,index) in sortFolderList3" :folderId="item.t_id" @click="selectFolder(item,$event,3,2)"><i class="iconfont icon-zhongzi_duigou gou-icon"></i>{{item.t_name}}</li>
                        </ul>
                    </div>
                </div>
                <!--四级组织-->
                <div class="tree-search">
                    <div class="search" @click.stop="addClass($event)" v-show="searchFolderList4.length>0">
                        <input class="over-search-input" type="text" placeholder="搜索组织" ref="searchInput4" @input="searchFolder($event,4)">
                        <i class="iconfont icon-zhongzi-zhankai down-icon" @click="openList(4,1)"></i>
                    </div>
                    <div class="search-list-div" v-show="searchFolderList4.length>0 && isShow4" @click.stop>
                        <ul class="search-result">
                            <li class="search-li" v-for="(item,index) in sortFolderList4" :folderId="item.t_id" @click="selectFolder(item,$event,4,2)"><i class="iconfont icon-zhongzi_duigou gou-icon"></i>{{item.t_name}}</li>
                        </ul>
                    </div>
                </div>
                <div class="tree-search">
                    <div class="search">
                        <input class="over-search-input" type="text" placeholder="搜索机泵" ref="searchMacInput" @input="searchMac">
                        <i class="iconfont icon-zhongzi-zhankai down-icon" @click.stop="openList(null,2)"></i>
                    </div>
                    <div class="search-list-div search-list-div2" v-show="searchPumpList.length>0 && isShow.macIsShow">
                        <ul class="search-result">
                            <li class="search-li" v-for="(item,index) in sortPumpList"  @click="selectMac(item,index)"><i class="iconfont icon-zhongzi_duigou gou-icon"></i>{{item.pump_name}}</li>
                        </ul>
                    </div>
                </div>
                <div class="search-pump-div" v-show="choosePosList.length>0 && !isShow.macIsShow">
                    <ul class="pump-result pump-result2">
                        <li class="pump-li" v-for="(pos,index) in choosePosList" @click="clickPump(pos,index)" draggable="true" @dragstart="dragstart(pos, index, $event,2)">
                            <i class="iconfont icon-zhongzi-shebeimoxing"></i>
                            <span class="pos_name">{{pos.pos_name}}</span>
                        </li>
                    </ul>
                </div>
                <div id="imgForm" class="upload" v-show="chooseMac.pump_id!=''">
                    <button class="btn" @click="monidianji">上传图片</button>
                    <input class="input-loc-img"  name="imgLocal" id="imgLocal" type='file' accept="image/*" @change="selectImg(2)"/>
                </div>
                <div class="upload2" v-show="overviewUrl!=''&& choosePosList.length>0">
                    <button class="btn" @click="keepOverview(3)">保存</button>
                </div>
            </div>
            <div class="overview-container">
                <div class="dragTips">请在此区域内拖动</div>
                <div class="overview-pic" id="overview-pic">
                    <div class="picTitle">
                        <input v-model="o_name" class="title-input" ref="fontInput">
                    </div>
                    <div id="move-block" class="move-block"  ref="bgPic" @drop.stop.prevent='drop($event,2)' @dragover.prevent='dragover' @click.stop='cancelClick' @contextmenu.prevent=''>
                        <vue-draggable-resizable
                                :w="item.width"
                                :h="item.height"
                                :x="0"
                                :y="0"
                                :min-width="70"
                                :min-height="50"
                                class-name="move-div"
                                @dragging="onDrag"
                                @resizing="onResize"
                                @resizestop="onResizstop(item,index)"
                                @dragstop ="onDragstop(item,index)"
                                v-for="(item,index) in MoveMsg"
                                :key="item.uid"
                                :style="item.style"
                                :custom-id="index"
                                @activated="onActivated(item,index)"
                        >
                            <input v-model="item.text" class="move-input" :style="item.inputStyle">
                            <div class="move"
                                 @contextmenu.prevent='rightClick(item, index, $event)'
                                 @dblclick="getPumpInfo(item)"
                            ></div>
                        </vue-draggable-resizable >
                        <vue-draggable-resizable
                                :w="item.circleD"
                                :h="item.circleD"
                                :x="0"
                                :y="0"
                                class-name="circle"
                                @dragging="onDragcircle"
                                @resizing="onResizecircle"
                                @resizestop="onResizstopcircle(item,index)"
                                @dragstop ="onDragstopcircle(item,index)"
                                v-for="(item,index) in MoveMsg"
                                :key="index"
                                :style="item.circleStyle"
                                :handles="['br']"
                                :title="item.text"
                        ></vue-draggable-resizable>
                        <ul class='rightBtn' v-show='isRightClick' ref='rightMsg'>
                            <li @click='addAsBasic()'>设为基准</li>
                            <li @click='delModelPos'>删除</li>
                        </ul>
                        <img :src="overviewUrl" class="overview-bg" ref='overviewBg' @dragstart.stop="stopDrag()">
                    </div>
                </div>
                <div class='alertBox alertTip' v-show='isTip'>
                    <div class='alertTitle'>提示  <i class="iconfont icon-zhongzi-guanbi icon-close"  @click='closeAlarm'></i></div>
                    <div class='alertText'>
                        请确认是否删除该部件？
                    </div>
                    <div class='yesOrNo'>
                        <button class='yes' @click='confirmTip(2)'>确定</button>
                        <button class='no' @click='closeAlarm'>取消</button>
                    </div>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    import {compareName} from "@/utils/utils";
    import MyContentTitle from 'components/common/MyContentTitle'
    import overviewCoal from 'components/common/overviewCoalCommon.js'
    import { ConvertPinyin } from 'assets/lib/pinyin.js'

    import VueDraggableResizable from 'vue-draggable-resizable'
    import 'vue-draggable-resizable/dist/VueDraggableResizable.css'
    export default {
        name: "OverviewPumpCoal",
        mixins: [overviewCoal],
        props:['parentToChild'],
        data(){
            return{
                title:"机泵总貌图设置",
                activeNum: "21",
                o_name:'',//总貌图名字
                chooseFolder:{
                    t_id:"",
                    t_name:"",
                    overview:null
                },
                chooseMac:{
                    ch_class: "",
                    mac_id: "",
                    pump_id: "",
                    pump_name: ""
                },
                choosePump:{
                    pump_name:"",
                    pump_id:"",
                    pump_type:""
                },
                choosePumpList:[],//选择的机组列表
                searchPumpList:[],//搜索到的机组列表
            }
        },
        created: function() {
            this.$emit('ievent',this.activeNum);
        },
        beforeRouteLeave (to, from, next) {
            if(to.name == 'overview'){
                let linkObj = {
                    chooseFolder:this.chooseFolder,
                    choosePump: this.chooseMac
                };
                sessionStorage.setItem('LinkConfig',JSON.stringify(linkObj));
            }
            else{
                if(sessionStorage.getItem('LinkConfig')){
                    sessionStorage.removeItem('LinkConfig');
                }
            }
            next();
        },
        mounted: function() {
            let linkInfo = {};
            if(!$.isEmptyObject(JSON.parse(sessionStorage.getItem('LinkConfig')))){//带入带出选项
                linkInfo = JSON.parse(sessionStorage.getItem('LinkConfig'));
            }
            if(!$.isEmptyObject(this.$route.params)){//双击图中的机泵跳转
                linkInfo = this.$route.params;
                this.activeNum = "21";
                this.$emit('ievent',this.activeNum);
            }
            if(!$.isEmptyObject(linkInfo)){
                this.chooseFolder=linkInfo.chooseFolder;
                this.chooseMac=linkInfo.choosePump;
                this.selectFolder(this.chooseFolder,null,1,2);
            }
            else{
                this.chooseFolder={
                    t_id:"",
                    t_name:"",
                }
            }
        },
        methods: {
            //搜索下拉列表
            searchFolder(e,type){
                let search_text = e.target.value;
                if(search_text.length>0){
                    let test_atr = new RegExp(search_text);
                    this[`searchFolderList${type}`]=[];
                    this[`isShow${type}`]=true;
                    let originList = this[`folderList${type}`];
                    for(let i=0;i<originList.length;i++){
                        let item = originList[i];
                        if(test_atr.test(item.t_name) || ConvertPinyin(item.t_name).indexOf(search_text)>=0){
                            this[`searchFolderList${type}`].push(item);
                        }
                    }
                    this.isShow.macIsShow=false;
                    if(this[`searchFolderList${type}`].length===0){
                        this.searchPumpList=[];
                        this.choosePosList=[];
                        this.$refs.searchMacInput.value ='';
                    }
                }
                else{
                    this[`searchFolderList${type}`]=this[`folderList${type}`].concat();
                    this.isShow1=false;
                    this.isShow2=false;
                    this.isShow3=false;
                    this.isShow4=false;
                    this.chooseFolder={
                        t_id:"",
                        t_name:"",
                        overview:null
                    };
                    this.chooseMac={
                        ch_class: "",
                        mac_id: "",
                        pump_id: "",
                        pump_name: ""
                    };
                    this.MoveMsg=[];
                    this.overviewUrl='';
                    this.choosePumpList=[];
                    this.searchPumpList=[];
                    this.choosePosList=[];
                    this.$refs.searchMacInput.value ='';
                }
            },
            //搜索机组下拉列表
            searchMac(e){
                let search_text = e.target.value;
                if(search_text.length>0){
                    let test_atr = new RegExp(search_text);
                    this.searchPumpList=[];
                    this.isShow.macIsShow=true;
                    this.isShow1=false;
                    this.isShow2=false;
                    this.isShow3=false;
                    this.isShow4=false;
                    for(let i=0;i<this.choosePumpList.length;i++){
                        if(test_atr.test(this.choosePumpList[i].pump_name) || ConvertPinyin(this.choosePumpList[i].pump_name).indexOf(search_text)>=0){
                            this.searchPumpList.push(this.choosePumpList[i]);
                        }
                    }
                }
                else{
                    this.chooseMac={
                        ch_class: "",
                        mac_id: "",
                        pump_id: "",
                        pump_name: ""
                    };
                    this.MoveMsg=[];
                    this.overviewUrl='';
                    this.choosePosList=[];
                    this.$refs.searchMacInput.value ='';
                }
            },
            //确定选择机组
            selectMac(chooseInfo,index){
                $('.search-list-div2 .search-li').eq(index).addClass('active').siblings().removeClass('active');
                this.chooseMac=chooseInfo;
                this.$refs.searchMacInput.value =this.chooseMac.pump_name;
                this.choosePosList=[];
                this.isShow.macIsShow=false;
                let that=this;
                let macObj={
                    mac_id:this.chooseMac.mac_id,
                    pump_id:this.chooseMac.pump_id,
                    ch_class:this.chooseMac.ch_class
                };
                this.$axios.post('queryPumpGoToPosition',macObj)
                    .then(res=>{
                        if(res.msg==0){
                            that.MoveMsg=[];
                            if(res.overview!=null && !$.isEmptyObject(res.overview)){
                                let result  = res.overview;
                                that.overviewUrl= result.bgurl;
                                if(result.o_name){
                                    that.o_name = result.o_name;
                                }
                                else{
                                    that.o_name = this.chooseMac.pump_name;
                                }
                                if(result.overviewFont){
                                    that.overviewFont = result.overviewFont;
                                }
                                else{
                                    that.overviewFont = 20;
                                }
                                // that.overviewUrl = require("assets/img/overview_pump.jpg");
                                // 图片地址
                                let img_url = result.bgurl;
                                // let img_url = require("assets/img/overview_pump.jpg");
                                // 创建对象
                                let img = new Image();
                                // 改变图片的src
                                img.src = img_url;
                                // 加载完成执行
                                img.onload = function() {
                                    /* 获取图片的真实宽高，计算背景图区域 */
                                    that.computePicWidth(img);
                                    let oriItemList = res.overview.itemList;
                                    if (oriItemList != null) {
                                        for (let i = 0; i < oriItemList.length; i++) {
                                            if (typeof oriItemList[i].circleD === "undefined") {
                                                oriItemList[i].circleD = 14;//设置小圆点的默认值为14
                                            }
                                            let obj = {
                                                index: i,
                                                text: oriItemList[i].itemName,//输入框中文字
                                                px: oriItemList[i].px,//框框百分比的x
                                                py: oriItemList[i].py,//框框百分比的y
                                                x: parseInt(oriItemList[i].px * that.origBlock.oriW),//页面的x
                                                y: parseInt(oriItemList[i].py * that.origBlock.oriH),//页面的y
                                                uid: oriItemList[i].uid,
                                                width: Number(oriItemList[i].width),//旧：机泵图标宽；新：框框的宽
                                                height: Number(oriItemList[i].height),//旧：机泵图标高；新：框框的高
                                                circleD: Number(oriItemList[i].circleD),//小圆的直径
                                                circleX: oriItemList[i].circleX,//小圆百分比的x
                                                circleY: oriItemList[i].circleY,//小圆百分比的y
                                                moveX: 0,//相对原来的框的移动位置x
                                                moveY: 0,//相对原来的框的移动位置y
                                                moveCircleX: 0,//圆点相对原来的框的移动位置x
                                                moveCircleY: 0,//圆点相对原来的框的移动位置y
                                                oriCircleX: parseInt(oriItemList[i].circleX * that.origBlock.oriW),//圆点初始x
                                                oriCircleY: parseInt(oriItemList[i].circleY * that.origBlock.oriH),//圆点初始y
                                                style: {
                                                    left: parseInt(oriItemList[i].px * that.origBlock.oriW) + 'px',
                                                    top: parseInt(oriItemList[i].py * that.origBlock.oriH) + 'px',
                                                    zIndex: 2,
                                                },
                                                circleStyle: {
                                                    left: parseInt(oriItemList[i].circleX * that.origBlock.oriW) + 'px',
                                                    top: parseInt(oriItemList[i].circleY * that.origBlock.oriH) + 'px',
                                                    zIndex: 2,
                                                },
                                                inputStyle:{//更新input的高
                                                    height:Number(oriItemList[i].height)/2 + 'px'
                                                },
                                            };
                                            for (let j = 0; j < res.position_list.length; j++) {
                                                let typeId = res.position_list[j].pos_type + '_' + res.position_list[j].pos_id
                                                if (oriItemList[i].uid == typeId) {
                                                    res.position_list[j].dragFlag = false;
                                                    obj.dragFlag = false;
                                                    that.MoveMsg.push(obj);
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            else{
                                that.overviewUrl='';
                            }
                            that.choosePosList = that.sortPositionsName(res.position_list);
                            for(let i=0;i<that.choosePosList.length;i++){
                                that.choosePosList[i].itemType=that.choosePosList[i].pos_type;
                                if(that.choosePosList[i].dragFlag === false){

                                }
                                else{
                                    that.choosePosList[i].dragFlag=true;
                                }
                            }
                        }
                        else if(res.msg==1){
                            console.log('参数为空!');
                        }
                        else if(res.msg==2){
                            console.log('后台运行错误！');
                        }
                        else if(res.msg==3){
                            that.overviewUrl='';
                            that.MoveMsg=[];
                            that.choosePosList=[];
                        }
                    })
                    .catch(err=>{
                        console.log('请求错误',err)
                    })
            },
        },
        components:{
            MyContentTitle,VueDraggableResizable
        }

    }

</script>

<style lang='scss'>
    @import '~assets/css/components/overviewCoal.scss'

</style>
